<!--  -->
<template>
  <div>
    <div class="mv">
      <div class="main">
        <div class="title">
          <p class="name" :title="`${mvInfo.name} - ${mvInfo.artist}`">{{mvInfo.name}} - {{mvInfo.artist}}</p>
          <p class="info">
            <span>
              <i class="iconfont icon-icon_play_"></i>
              {{mvInfo.mvPlayCnt|fansCnt}}
            </span>
            发布时间：
            <span>{{mvInfo.releaseDate}}</span>
          </p>
        </div>
        <div class="video">
          <video :src="mvUrl" controls style="width:100%"></video>
        </div>
      </div>
    </div>
    <div class="related_mv">
      <div class="related_mv_tit">推荐MV</div>
      <music-list :dataList="relatedMvList">
        <template v-slot:play_cnt="props">
          <p class="play_cnt">
            <span>
              <span>
                <i class="iconfont icon-icon_play_"></i>
                {{props.item.mvPlayCnt|fansCnt}}
              </span>
            </span>
            <span>{{props.item.songTimeMinutes}}</span>
          </p>
          <div class="shadow"></div>
        </template>
        <template v-slot:info="props">
          <p class="name">
            <span :title="props.item.name" @click="goPage('mvPlay',props.item.id)">{{props.item.name}}</span>
          </p>
          <p class="artist">
            <span :title="props.item.artist">{{props.item.artist}}</span>
          </p>
        </template>
      </music-list>
    </div>
  </div>
</template>

<script>
import { HttpManager } from '../api';
import MusicList from '../components/MusicList.vue';
import mixin from '../mixins';
export default {
  name: "MvPlay",
  mixins:[mixin],
  data () {
    return {
      mvUrl: '',
      mvInfo: {},
      relatedMvList: [],
    };
  },

  components: { MusicList },

  methods: {
    getMvUrl () {
      let that = this;
      HttpManager.getPlayUrl({ mid: this.$route.params.id, type: 'mv' })
        .then((res) => {
          that.mvUrl = res.data.url;
        });
      HttpManager.getSongInfo(this.$route.params.id)
        .then((res) => {
          that.mvInfo = res.data;
          that.getRelateMvList(that.mvInfo.artistid)
        })
        .catch(() => { });
    },
    getRelateMvList (id) {
      let that = this;
      HttpManager.getSingerMv({ artistid: id, pn: 1, rn: 5 })
        .then((res) => {
          that.relatedMvList = res.data.mvlist;
        })
    },
    goMvPlayPage (mid) {
      this.$router.push({ path: '/mvPlay/' + mid });
    },
  },

  computed: {},
  filters: {
    fansCnt (val) {
      let num = parseInt(val);
      if (num < 10000) {
        return num;
      } else {
        return (num / 10000).toFixed(1) + "万"; //取小数点后一位
      }
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getMvUrl();
  },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { },
}

</script>
<style lang="scss" scoped>
@import "../assets/css/font";

.mv {
  background-color: #333;
  position: relative;
  top: 0;
  left: -120px;
  width: 100vw;
  min-width: 990px;
  // min-height: calc(100vh - 200px);
  max-width: 1490px;
  z-index: 0;
  padding-bottom: 40px;
  padding-top: 10px;
  .main {
    padding: 0 120px;
    .title {
      margin-bottom: 10px;
      color: #fff;
      .name {
        font-size: 20px;
        line-height: 30px;
        height: 30px;
        margin: 10px 0;
        display: block;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
      .info {
        font-size: 14px;
        color: #bbb;
        span {
          margin-right: 20px;
        }
      }
    }
  }
}
.related_mv {
  padding-top: 20px;
  .related_mv_tit {
    font-size: 22px;
    font-weight: 600;
    margin-bottom: 20px;
  }
  .play_cnt {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 3px 5px;
    background-color: rgba(2, 2, 2, 0.3);
    color: #fff;
    font-weight: 400;
    font-size: 13px;
    line-height: 18px;
    z-index: 10;
  }
  .name {
    font-size: 18px;
    font-weight: 400;
    line-height: 25px;
    margin-top: 10px;
    display: block !important;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    span {
      cursor: pointer;
    }
  }
  .artist {
    color: #999;
    font-weight: 300;
    margin-top: 5px;
    margin-bottom: 26px;
    font-size: 14px;
    line-height: 1;
    font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
      sans-serif;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
}
.flex_c {
  display: flex;
  align-items: center;
}
</style>
<style lang="scss">
.related_mv {
  .item {
    .cover_out {
      padding-bottom: 59% !important;
    }
  }
}
</style>



